.navigation-menu {
  width: 320px;
  height: 509px;
  padding-top: 0;
  display: none;
  position: fixed;
  background-color: rgba(255, 255, 255, 0.9);
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  overflow: hidden;
  z-index: 10;
  margin-top: 58px;
  background-image: url("../img/blur-mobile.jpg");

  // filter: progid:DXImageTransform.Microsoft.Blur(pixelradius=50, enabled='true');
  // -ms-filter: "progid:DXImageTransform.Microsoft.Blur(pixelradius=50, enabled='true')";
  // filter: blur(50px);
}

.navigation-menu_show {
  display: block;
}

.navigation-menu__wrapper {
  width: 231px;
  margin: 21px auto 0;
  font-size: 24px;
  line-height: 29px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  font-family: Museo Sans Cyrl;

  & > a {
    width: 184px;
    padding-left: 47px;
    background-image: url(../img/phone1x.png);
    background-repeat: no-repeat;
    background-position: left 50%;
    background-size: 24px 24px;
    font-weight: 600;
  }

  & > a {
    font-weight: 600;
  }

  & > a:hover {
    opacity: 0.7;
    outline: none;
  }

  & > a:focus {
    opacity: 0.4;
    outline: none;
  }

}

@media (min-width: @desktop) {
  .navigation-menu {
    width: 1920px;
    height: 742px;
    margin-top: 108px;
    padding-top: 110px;
    background-image: url("../img/blur-desktop.jpg");
  }

  .navigation-menu__wrapper {
    width: 340px;
    margin-top: 64px;

    & > a {
      display: none;
    }
  }
}
