.main-index {
  width: 100%;
}

.main-index__wrapper {
  width: 320px;
  margin: 0 auto;
}

.main-index__head {
  padding: 0 10px 13px;
  background-image: url("../img/background-mobile1x.jpg");
  color: @white;
  background-repeat: no-repeat;
  background-size: 320px 300px;

  @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
    background-image: url("../img/background-mobile2x.jpg");
  }
}

.main-index__header {
  font-family: Bebas Neue;
  font-size: 50px;
  line-height: 50px;
  text-transform: uppercase;
  margin: 0;
  padding-top: 151px;
  margin-bottom: 12px;
}

.main-index__header-description {
  font-family: Museo Sans Cyrl;
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
}

.main-index__header-description_red {
  color: @style-red;
}

@media (min-width: @desktop) {
  .main-index__wrapper {
    width: 1920px;
  }

  .main-index__head {
    position: relative;
    z-index: -5;
    padding-left: 89px;
    padding-bottom: 628px;
    background-image: url("../img/background-desktop1x.jpg");
    background-size: 1920px 960px;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/background-desktop2x.jpg");
    }
  }

  .main-index__header {
    font-size: 90px;
    line-height: 90px;
    margin-bottom: 20px;
    padding-top: 177px;
  }

  .main-index__header-description {
    font-size: 50px;
    line-height: 60px;
    font-weight: 600;
  }
}
