.slider__wrapper-decorative {
  display: flex;
  justify-content: center;
  overflow: hidden;
}

.slider__wrapper {
  width: 320px;
  display: flex;
  transition: transform 0.6s ease;
}

.slider__item {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: 0 auto;
  flex: 0 0 100%;
}

@media (min-width: @desktop) {
  .slider {
    width: 1739px;
    margin: 0 auto;
    padding-right: 7px;
  }

  .slider__wrapper-decorative {
    width: 1739px;
    position: relative;
    margin: 0 auto 61px;

    &::before,
    &::after {
      content: "";
      position: absolute;
      width: 22px;
      height: calc(100% - 60px);
      background-color: transparent;
      border: 30px solid @style-red;
      border-right: none;
      z-index: 10;
    }

    &::before {
      left: 0;
    }

    &::after {
      top: 0;
      right: 0;
      transform: rotate(180deg);
    }
  }

  .slider__wrapper {
    width: 1635px;
    display: flex;
  }

  .slider__item {
    margin: 0;
  }
}
