.year-changer {
  display: flex;
  flex-wrap: wrap;
  padding: 0 10px;
  justify-content: center;
}

.year-changer input {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  position: absolute;
}

.year-changer input:checked + label {
  background-color: @style-red;
  color: white;
  border: 1px solid @style-red;
}

.year-changer label {
  width: 63px;
  font-family: Museo Sans Cyrl;
  font-size: 13px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.155em;
  text-transform: uppercase;
  color: @font-grey;
  font-weight: 600;
  border: 1px solid #888888;
  box-sizing: border-box;
  border-radius: 4px;
  background-color: white;
  margin: 5px;
  padding: 11px 0;
  cursor: pointer;

  &:hover {
    background-color: @style-red;
    color: white;
    opacity: 0.7;
  }
}

@media (min-width: @desktop) {
  .year-changer {
    display: none;
  }
}
