.social {
  padding: 0;
  margin: 0;
  list-style: none;
  width: 96px;
  display: flex;
  justify-content: space-between;
}

.social__image {
  display: flex;
  border: none;

  &:hover {
    opacity: 0.7;
  }

  &:focus {
    opacity: 0.4;
    outline: none;
  }

  img {
    border: none;
  }
}

@media (min-width: @desktop) {
  .social {
    // display: none;
  }
}
