.slider-changer {
  width: 196px;
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  align-items: center;
}

.slider-changer__arrow {
  padding: 0;
  border: none;
  background-color: transparent;
  display: flex;
  cursor: pointer;
  fill: @font-grey;

  &:hover {
    fill: @style-red;
  }

  &:focus {
    fill: @style-red;
    opacity: 0.7;
    outline: none;
  }
}

.slider-changer__arrow_left {
  transform: rotate(180deg);
}

.slider-changer__counter {
  font-family: Museo Sans Cyrl;
  font-size: 24px;
  line-height: 29px;
  color: @font-black;
  font-weight: 600;
}

@media (min-width: @desktop) {
  .slider-changer {
    width: 223px;
  }
}
