.second-block {
  width: 320px;
  margin: 0 auto 64px;
}

.second-block__header {
  font-family: Bebas Neue;
  font-size: 40px;
  line-height: 40px;
  text-transform: uppercase;
  color: @style-red;
  text-align: center;
  margin-bottom: 39px;
  margin-top: 38px;
}

@media (min-width: @desktop) {
  .second-block {
    width: 1920px;
    margin-bottom: 85px;
  }

  .second-block__header {
    font-size: 70px;
    line-height: 70px;
    margin-bottom: 67px;
  }
}
