.winners-list {
  list-style: none;
  margin: 0;
  margin-top: 31px;
  padding-left: 78px;
}

.winners-list__item {
  font-family: Museo Sans Cyrl;
}

.winners-list__item::after {
  content: "";
  position: absolute;
  width: 15px;
  height: 15px;
  left: -28px;
  top: 30%;
  background-size: 15px 15px;
}

.winners-list__item:first-of-type {
  font-size: 20px;
  line-height: 24px;
  color: @white;
  background-color: @style-red;
  border-radius: 4px;
  display: inline-block;
  align-items: center;
  padding: 4px 16px 6px 5px;
  margin-bottom: 13px;
}

.winners-list__item:not(:first-of-type) {
  font-size: 14px;
  line-height: 17px;
  padding-top: 13px;
  padding-bottom: 14px;
  font-weight: 600;
  margin-bottom: 7px;
}

.winners-list__item {
  position: relative;
}

.winners-list__item:first-of-type::before {
  content: "";
  width: 47px;
  height: 35px;
  background-image: url("../img/first-place-award.png");
  position: absolute;
  left: -78px;
  top: 0;
  background-size: 47px 35px;

  @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
    background-image: url("../img/first-place-award2x.png");
  }
}

.winners-list__item:nth-of-type(2)::before,
.winners-list__item:nth-of-type(4)::before {
  content: "";
  position: absolute;
  width: 27px;
  height: 30px;
  background-image: url("../img/second-place-award.png");
  left: -68px;
  top: 9px;
  background-size: 27px 30px;


  @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
    background-image: url("../img/second-place-award2x.png");
  }
}

.winners-list__item:nth-of-type(3)::before {
  content: "";
  position: absolute;
  width: 23px;
  height: 27px;
  background-image: url("../img/medal1x.png");
  left: -66px;
  top: 8px;
  background-size: 23px 27px;


  @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
    background-image: url("../img/medal2x.png");
  }
}

.winners-list__item_worldwide::after {
  background-image: url("../img/worldwide1x.png");

  @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
    background-image: url("../img/worldwide2x.png");
  }
}

.winners-list__item_europe::after {
  background-image: url("../img/european-union1x.png");

  @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
    background-image: url("../img/european-union2x.png");
  }
}

.winners-list__item_russia::after {
  background-image: url("../img/russia1x.png");

  @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
    background-image: url("../img/russia2x.png");
  }
}

@media (min-width: @desktop) {
  .winners-list {
    order: 1;
    margin-top: -579px;
    padding-left: 147px;
  }

  .winners-list__item:first-of-type {
    font-size: 36px;
    line-height: 43px;
    padding: 4px 6px 3px 9px;
    font-weight: 600;
    margin-bottom: 23px;
  }

  .winners-list__item:not(:first-of-type) {
    font-size: 24px;
    line-height: 29px;
    color: white;
    margin-bottom: 20px;
  }

  .winners-list__item:first-of-type::before {
    width: 70px;
    height: 52px;
    left: -142px;
    top: 0;
    background-image: url("../img/first-place-award-desktop1x.png");
    background-size: 70px 52px;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/first-place-award-desktop2x.png");
    }
  }

  .winners-list__item:nth-of-type(2)::before,
  .winners-list__item:nth-of-type(4)::before {
    width: 38px;
    height: 44px;
    left: -126px;
    top: 10px;
    background-image: url("../img/second-place-award-desktop1x.png");
    background-size: 38px 44px;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/second-place-award-desktop2x.png");
    }
  }

  .winners-list__item:nth-of-type(3)::before {
    width: 32px;
    height: 40px;
    left: -123px;
    top: 7px;
    background-image: url("../img/medal-desktop1x.png");
    background-size: 32px 40px;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/medal-desktop2x.png");
    }
  }

  .winners-list__item_worldwide::after,
  .winners-list__item_europe::after,
  .winners-list__item_russia::after {
    left: -73px;
    top: 30%;
  }

  .winners-list__item_worldwide::after {
    width: 22px;
    height: 22px;
    background-image: url("../img/worldwide-desktop1x.png");
    background-size: 22px 22px;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/worldwide-desktop2x.png");
    }
  }
  .winners-list__item_europe::after {
    width: 22px;
    height: 22px;
    background-image: url("../img/european-union-desktop1x.png");
    background-size: 22px 22px;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/european-union-desktop2x.png");
    }
  }
  .winners-list__item_russia::after {
    width: 22px;
    height: 22px;
    background-image: url("../img/russia-desktop1x.png");
    background-size: 22px 22px;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/russia-desktop2x.png");
    }
  }
}
