.third-block {
  width: 100%;
  background: linear-gradient(180deg, #FDF4F4 0%, #ECF3EC 100%);
}

.third-block__wrapper {
  width: 320px;
  margin: 0 auto;
  padding-top: 37px;
  background: linear-gradient(180deg, #FDF4F4 0%, #ECF3EC 100%);
}

.third-block__header {
  font-family: Bebas Neue;
  font-size: 40px;
  line-height: 40px;
  text-align: center;
  text-transform: uppercase;
  color: @style-red;
  margin: 0 0 26px;
}

@media (min-width: @desktop) {
  .third-block__header {
    font-size: 70px;
    line-height: 70px;
    margin-bottom: 45px;
    margin-right: 11px;
  }

  .third-block__wrapper {
    width: 1920px;
    padding-top: 64px;
  }
}
