  color: #000000;
}
.body-overflow-hidden {
  overflow: hidden;
}
.header {
  width: 100%;
  position: fixed;
  z-index: 50;
}
.header__wrapper {
  width: 320px;
  padding: 11px 9px 13px 11px;
  position: relative;
  margin: 0 auto;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__logo {
  display: flex;
}
.header__logo svg {
  fill: white;
}
.header__social {
  display: none;
}
.header__social > a {
  font-weight: 600;
}
.header__social > a:hover {
  opacity: 0.7;
  outline: none;
}
.header__social > a:focus {
  opacity: 0.4;
  outline: none;
}
.header__hamburger {
  padding: 0;
  margin: 0;
  width: 35px;
  height: 35px;
  border: none;
  background-color: transparent;
  position: relative;
  cursor: pointer;
}
.header__hamburger::before,
.header__hamburger::after {
  content: "";
  width: 35px;
  height: 2px;
  background-color: #ffffff;
  display: block;
  position: absolute;
}
.header__hamburger::before {
  top: 28%;
}
.header__hamburger::after {
  top: 61%;
}
.header__hamburger:hover::after,
.header__hamburger:hover::before {
  background-color: #FF3754;
}
.header__hamburger:focus {
  outline: none;
}
.header__hamburger:focus::before,
.header__hamburger:focus::after {
  background-color: #FF3754;
  opacity: 0.4;
}
.header__hamburger_open::before,
.header__hamburger_open::after {
  transition-duration: 0.1s;
  top: 50%;
}
.header__hamburger_open::before {
  transform: rotate(45deg);
}
.header__hamburger_open::after {
  transform: rotate(-45deg);
}
.header__hamburger_changed::before,
.header__hamburger_changed::after {
  background-color: #000000;
}
@media (min-width: 1920px) {
  .header__wrapper {
    width: 1920px;
    padding: 25px 85px 26px 94px;
  }
  .header__logo svg {
    width: 219px;
    height: 58px;
  }
  .header__social {
    display: flex;
    justify-content: space-between;
    width: 367px;
    box-sizing: border-box;
    margin-left: 990px;
  }
  .header__social > a {
    color: white;
    font-family: Museo Sans Cyrl;
    font-size: 24px;
