    background-repeat: no-repeat;
    background-position: left 50%;
    align-self: center;
    background-size: 24px 24px;
  }
  .header__hamburger {
    width: 50px;
    height: 50px;
  }
  .header__hamburger::before {
    top: 37%;
  }
  .header__hamburger::after {
    top: 60%;
  }
  .header__hamburger::before,
  .header__hamburger::after {
    width: 50px;
  }
  .header__hamburger_open::before,
  .header__hamburger_open::after {
    top: 50%;
  }
}
