.footer__wrapper {
  box-sizing: border-box;
  width: 320px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 14px 13px 17px;
}

.footer__copyright_mobile {
  font-family: Museo Sans Cyrl;
  font-size: 18px;
  line-height: 27px;
  color: @font-black;
  font-weight: 600;
}

.footer__copyright_desktop {
  display: none;
}

.footer__logo {
  display: flex;

  &:hover {
    opacity: 0.7;
  }

  &:focus {
    opacity: 0.4;
    outline: none;
  }
}

@media (min-width: @desktop) {
  .footer {
    width: 100%;
  }

  .footer__wrapper {
    width: 1920px;
    box-sizing: border-box;
    margin: 0 auto;
    padding: 17px 91px 13px 88px;
  }

  .footer__copyright_mobile {
    display: none;
  }

  .footer__copyright_desktop {
    display: inline;
    font-family: Museo Sans Cyrl;
    font-size: 18px;
    line-height: 27px;
  }
}
