.text-block {
  font-family: Museo Sans Cyrl;
  line-height: 163%;
  display: flex;
  flex-direction: column;
  padding: 0 10px;
  margin-bottom: 30px;
  font-size: 16px;
  width: 300px;
}

.text-block__header {
  font-weight: 600;
  margin-top: 36px;
  font-size: 18px;
}

@media (min-width: @desktop) {
  .text-block {
    padding-left: 0;
    width: 670px;
    line-height: 29px;
    margin-top: 9px;
    margin-bottom: 8px;
    padding-right: 0;
  }

  .text-block__content {
    letter-spacing: 1px;
  }

  .text-block__header {
    margin-top: 22px;
  }
}
