.personal-qualities {
  width: 291px;
  list-style: none;
  padding-left: 29px;
  margin: 25px 0 8px;
}

.personal-qualities li {
  font-family: Museo Sans Cyrl;
  font-size: 18px;
  line-height: 160%;
  color: @font-black;
  position: relative;
  margin-bottom: 31px;
}

.personal-qualities li:nth-of-type(n+5) {
  display: none;
}

.personal-qualities li::before {
  content: "";
  position: absolute;
  width: 10px;
  height: 10px;
  background-color: @style-red;
  border-radius: 3px;
  top: 9px;
  left: -20px;
}

@media (min-width: @desktop) {
  .personal-qualities {
    width: 700px;
    padding-left: 39px;
    margin-bottom: 0;
  }

  .personal-qualities li {
    margin-bottom: 1px;
  }

  .personal-qualities li:nth-of-type(n+5) {
    display: list-item;
  }

  .personal-qualities li:before {
    left: -39px;
  }


}
