.header {
  width: 100%;
  position: fixed;
  z-index: 50;
}

.header__wrapper {
  width: 320px;
  padding: 11px 9px 13px 11px;
  position: relative;
  margin: 0 auto;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo {
  display: flex;
}

.header__logo svg {
  fill: white;
}

.header__social {
  display: none;

  & > a {
    font-weight: 600;
  }

  & > a:hover {
    opacity: 0.7;
    outline: none;
  }

  & > a:focus {
    opacity: 0.4;
    outline: none;
  }
}

.header__hamburger {
  padding: 0;
  margin: 0;
  width: 35px;
  height: 35px;
  border: none;
  background-color: transparent;
  position: relative;
  cursor: pointer;

  &::before, &::after {
    content: "";
    width: 35px;
    height: 2px;
    background-color: @white;
    display: block;
    position: absolute;
  }

  &::before {
    top: 28%;
  }

  &::after {
    top: 61%;
  }

  &:hover::after,
  &:hover::before {
    background-color: @style-red;
  }

  &:focus {
    outline: none;
  }

  &:focus::before,
  &:focus::after {
    background-color: @style-red;
    opacity: 0.4;
  }
}

.header__hamburger_open {
  &::before,
  &::after {
    transition-duration: 0.1s;
    top: 50%;
  }

  &::before {
    transform: rotate(45deg);
  }
  &::after {
    transform: rotate(-45deg);
  }
}

.header__hamburger_changed::before,
.header__hamburger_changed::after {
  background-color: #000000;
}

@media (min-width: @desktop) {
  .header__wrapper {
    width: 1920px;
    padding: 25px 85px 26px 94px;
  }

  .header__logo svg {
    width: 219px;
    height: 58px;
  }

  .header__social {
    display: flex;
    justify-content: space-between;
    width: 367px;
    box-sizing: border-box;
    margin-left: 990px;
  }

  .header__social > a {
    color: white;
    font-family: Museo Sans Cyrl;
    font-size: 24px;
    line-height: 29px;
    padding-left: 43px;
    background-image: url("../img/phone1x.png");
    background-repeat: no-repeat;
    background-position: left 50%;
    align-self: center;
    background-size: 24px 24px;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/phone2x.png");
    }
  }

  .header__hamburger {
    width: 50px;
    height: 50px;

    &::before {
      top: 37%;
    }

    &::after {
      top: 60%;
    }

    &::before, &::after {
      width: 50px;
    }
  }

  .header__hamburger_open {
    &::before,
    &::after {
      top: 50%;
    }
  }
}
