.partners {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  list-style: none;
  padding: 0 10px 43px;
  margin: 0 auto;
  box-sizing: border-box;
  margin-top: 8px;
}

.partners__item {
  width: 320px;

  a {
    display: block;

    &:hover {
      opacity: 0.7;
    }

    &:focus {
      opacity: 0.4;
      outline: none;

    }
  }
}

.partners__image {
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.15);
  margin-top: 22px;
  margin-bottom: 15px;
  border: none;
}

.partners__description {
  font-family: Museo Sans Cyrl;
  display: flex;
  flex-direction: column;
  line-height: 26px;
}

.partners__title {
  font-size: 18px;
  color: @font-black;
  font-weight: 600;
  margin-bottom: 11px;
  line-height: 29px;
}

@media (min-width: @desktop) {
  .partners {
    display: flex;
    flex-wrap: wrap;
    padding-left: 8px;
    padding-bottom: 86px;
  }

  .partners__item {
    width: 412px;
    padding: 0 16px;
    margin-bottom: 58px;
  }

  .partners__image {
    width: 412px;
    height: 240px;
    margin-bottom: 23px;
  }

  .partners__content {
    font-size: 18px;
    line-height: 29px;
  }

}
