.winners {
  padding-top: 13px;
  padding: 13px 10px;
  width: 300px;
}

.winners__button {
  font-family: Museo Sans Cyrl;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.155em;
  text-transform: uppercase;
  background: #FF3754;
  border: 1px solid #FF3754;
  box-sizing: border-box;
  border-radius: 4px;
  color: @white;
  padding: 23px 0 24px;
  width: 300px;
  cursor: pointer;

  &:hover {
    opacity: 0.7;
  }

  &:focus {
    opacity: 0.4;
    outline: none;
  }
}

@media (min-width: @desktop) {
  .winners {
    display: flex;
    flex-direction: column;
    width: auto;
    padding-left: 89px;
  }

  .winners__button {
    width: 412px;
    font-size: 19px;
    line-height: 23px;
    order: 2;
    margin-top: 98px;
    padding: 20px 0 22px;
  }
}
