.navigation-list {
  list-style: none;
  padding: 0;
  font-size: 20px;
  line-height: 24px;
  font-weight: 600;
  margin: 22px auto 54px;

  li {
    display: flex;
    justify-content: center;
    margin-bottom: 15px;
  }
}

.navigation-list__item {
  &:focus {
    outline: none;
    opacity: 0.4;
  }

  &:hover {
    color: @style-red;
  }
}

@media (min-width: @desktop) {
  .navigation-list {
    font-size: 50px;
    line-height: 60px;

    li {
      margin-bottom: 37px;
    }
  }
}
