.personal-info {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.personal-info__data {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.personal-info__names {
  width: 290px;
  font-family: Bebas Neue;
  display: block;
  flex-direction: column;
  align-items: center;
  margin-top: 18px;
  margin-bottom: 27px;
}

.personal-info__surname {
  font-size: 40px;
  line-height: 40px;
  text-transform: uppercase;
  color: @style-red;
  margin-bottom: 15px;
  text-align: center;
}

.personal-info__name {
  font-size: 30px;
  line-height: 30px;
  text-transform: uppercase;
  color: @font-grey;
  text-align: center;
}

.personal-info__role {
  font-family: Museo Sans Cyrl;
  font-size: 13px;
  line-height: 16px;
  letter-spacing: 0.155em;
  text-transform: uppercase;
  color: @font-black;
  margin-bottom: 43px;
  text-align: center;
}

.personal-info__hide-button {
  width: 156px;
  box-sizing: border-box;
  font-family: Museo Sans Cyrl;
  font-size: 13px;
  line-height: 16px;
  letter-spacing: 0.155em;
  text-transform: uppercase;
  color: @font-grey;
  background-color: white;
  border: 1px solid @font-grey;
  border-radius: 4px;
  padding: 11px 46px;
  cursor: pointer;

  &:hover {
    background-color: @style-red;
    color: white;
  }

  &:focus {
    background-color: @style-red;
    opacity: 0.7;
    outline: none;
  }
}

@media (min-width: @desktop) {
  .personal-info {
    flex-direction: row-reverse;
    align-items: flex-start;
    width: 1443px;
    margin: 0 auto;

    img {
      width: 550px;
      height: 550px;
    }
  }

  .personal-info__data {
    align-items: flex-start;
    flex-grow: 1;
  }

  .personal-info__names {
    align-items: flex-start;
    margin-bottom: 22px;
    margin-top: 30px;
  }

  .personal-info__surname {
    font-size: 70px;
    line-height: 70px;
    margin-bottom: 5px;
    text-align: left;
  }

  .personal-info__name {
    font-size: 40px;
    line-height: 40px;
    text-align: left;
  }

  .personal-info__role {
    margin-bottom: 15px;
  }

  .personal-info__hide-button {
    display: none;
  }
}
